* {
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #f15946;
    transition: 0.4s ease-in-out;
  }
  
  html,
  body,
  #root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  .scrollArea {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: auto;
  }
  
  .container {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .VertContainer {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: block;
    // align-items: center;
    // justify-content: center;
    text-align: center;
  }
  
  header {
    position: absolute;
    z-index: 99;
    width: 100%;
    padding: 60px;
    .header-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .logo {
        font-weight: 900;
        letter-spacing: 2px;
        color: #fff;
      }
      nav {
        ul {
          margin: 0;
          padding: 0;
          display: flex;
          li {
            list-style: none;
            margin: 0 60px;
            &.btn {
              a {
                color: #fff;
                font-weight: 600;
                background: #23232a;
                padding: 16px 24px;
                border-radius: 10px;
              }
            }
            a {
              text-transform: capitalize;
              text-decoration: none;
              color: #fff;
            }
          }
        }
      }
    }
  }
  
  
  .introTitle {
    font-size: 10rem;
    text-align: left;
    width: 100%;
    margin: 0 auto;
    color: #fff;
    span {
      display: inline-block;
      text-align: left;
    }
    @media (max-width: 1440px) {
      font-size: 6rem;
    }
  }
  
  .title {
    font-size: 10rem;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    color: #fff;
    span {
      display: block;
    }
    @media (max-width: 1440px) {
      font-size: 6rem;
    }
  }
  
  .subText {
    font-size: 2rem;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    color: #fff;
    span {
      display: block;
    }
    @media (max-width: 1440px) {
      font-size: 1rem;
    }
  }
  
  .loading {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: #171717;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .loading-bar-container {
    width: 100px;
    height: 32px;
    background: #272727;
  }
  
  .loading-bar {
    height: 32px;
    background: #f15946;
  }
  
  .box{
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }
  
  .custLink {
    color: rgb(0, 99, 228);
    font-size: 1.3rem;
    text-decoration: none;
    margin-top: 1em;
    display: inline-block;
    font-weight: 300;
    position: relative;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    // padding: .5em;
    // margin-left: ;
  
    &:before, &:after {
      position: absolute;
      content: '';
      border-bottom: 3px solid rgb(0, 99, 228);
      border-radius: 1em;
      bottom: .3em;
      margin-bottom: -7px;
      transition: transform .5s cubic-bezier(0.075, 0.82, 0.165, 1);
    }
  
    &:before {
      width: 5.9em;
      transform-origin: left;
    }
  
    &:after {
      width: 5em;
      transform-origin: left;
      transform: translateX(10%);
      visibility: hidden;
    }
  
    &:hover::before {
      transform: scaleX(0.1);
    }
  
    &:hover::after {
      transform: translateX(-100%);
      border-bottom: 3px solid rgb(255, 255, 255);
      visibility: visible;
    }
  }
  
  .lineBtn {
      color: #fff;
      font-weight: 200;
      font-size: 1rem;
      background: #23232a;
      padding: 8px 14px;
      border-radius: 50px;
      text-decoration: none;
  }
  